.calendar {
  display: flex;
  flex-flow: column;
}

.calendar .header .month-year {
  font-size: clamp(18px, 4vw, 25px);
  font-weight: bold;
  color: #29292F;
  padding: 20px 0;
}

.calendar .days {
  display: flex;
  flex-flow: wrap;
}

.calendar .days .day_name {
  width: calc(100% / 7);
  padding: 20px;
  font-size: clamp(14px, 2vw, 16px);
  font-weight: bold;
  color: #6E6E74;
  display: flex;
  justify-content: center;
}

.calendar .days .day_name:nth-child(7) {
  border: none;
}

.calendar .days .day_num {
  display: flex;
  flex-flow: column;
  width: calc(100% / 7);
  font-weight: bold;
  color: #29292F;
  cursor: pointer;
  position: relative;
  z-index: 0;
  transition: 350ms ease;
}

.calendar .days .day_num:hover .event {
  background-color: #fd9c27;
}

.calendar .days .day_num span {
  display: inline-flex;
  width: 100%;
  padding: 15px;
  justify-content: center;
  font-size: clamp(16px, 4vw, 20px);
}

.calendar .days .day_num .event {
  position: absolute;
  border-radius: 50%;
  background-color: #FB8C05;
  width: 40px;
  height: 40px;
  z-index: -1;
  top: 50%;
  right: 50%;
  transform: translate(50%, -50%);
  transition: 350ms ease;
}

.calendar .days .day_num .event.green {
  background-color: #51ce57;
}

.calendar .days .day_num .event.blue {
  background-color: #518fce;
}

.calendar .days .day_num .event.red {
  background-color: #ce5151;
}

.calendar .days .day_num.ignore {
  background-color: #fdfdfd;
  color: #ced2d4;
  cursor: inherit;
  text-align: center;
  opacity: 0;
}

.calendar .days .day_num.selected {
  cursor: inherit;
  color: #6E6E74;
}

.calendar .days .day_num.selected span {
  position: relative;
}

.calendar .days .day_num.selected span::after {
  content: '';
  position: absolute;
  top: 50%;
  right: 50%;
  background-color: #F5F5F5;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  z-index: -1;
  transform: translate(50%, -50%);
}

.calendar__events {
  position: relative;
  padding: 100px 0;
  overflow: hidden;
}

.calendar__events .calendar__bg-image {
  position: absolute;
  top: 6px;
  right: 0;
}

@media (max-width: 991px) {
  .calendar__events .calendar__bg-image {
    left: 50%;
  }
}

.calendar__events .calendar__events-section-title {
  margin-top: 0;
}

@media (max-width: 420px) {
  .calendar__events .row {
    margin: 0 auto;
  }
  .calendar__events .calendar__event {
    padding: 0;
  }
  .calendar__events .calendar__info {
    padding: 0;
  }
  .calendar__events .calendar {
    padding: 15px;
  }
}

.calendar__event-info {
  background-color: #007540;
  color: #ffffff;
  padding: 30px;
  height: 100%;
}

.calendar__event-info--wrap {
  padding-top: 50px;
}

.calendar__event-info--wrap a {
  color: #ffffff;
}

.calendar__event-info--wrap a h3 {
  margin: 0;
  font-size: 22px;
  font-weight: 400;
}

.calendar__event-info--wrap .event-date {
  font-size: 14px;
  padding-top: 10px;
  padding-bottom: 20px;
}

.calendar {
  background-color: #ffffff;
  padding: 40px 80px;
}

.calendar .header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.calendar .prev-month,
.calendar .next-month {
  color: #29292F;
  font-size: 30px;
  font-weight: 600;
  padding: 0 20px;
  cursor: pointer;
}

@media (max-width: 991px) {
  .calendar {
    padding: 40px 0;
  }
}

.rtl .calendar__events .calendar__bg-image {
  position: absolute;
  top: 6px;
  left: 0;
  right: inherit;
}

@media (max-width: 991px) {
  .rtl .calendar__events .calendar__bg-image {
    right: 50%;
  }
}

.rtl .calendar .header {
  flex-direction: row-reverse;
}
